<template>
    <div class="container back-one">
        <div class="withdrawal-pass">
            <ul>
                <li v-if="UserSession.hasSetDrawPassword">
                    <div class="fl">
                        <i class="ico-i1"></i>
                        <span>旧密码：</span>
                    </div>
                    <input v-model="oldPassword" maxlength="22" type="password" placeholder="请输入密码">
                </li>
                <li v-else>
                    <div class="fl">
                        <i class="ico-i1"></i>
                        <span>登录密码：</span>
                    </div>
                    <input v-model="oldPassword" maxlength="22" type="text" style="-webkit-text-security:disc;" placeholder="请输入密码">
                </li>
                <li>
                    <div class="fl">
                        <i class="ico-i2"></i>
                        <span>新取款密码：</span>
                    </div>
                    <input v-model="newPassword" maxlength="22" type="text" style="-webkit-text-security:disc;" placeholder="请输入新取款密码">
                </li>
            </ul>
            <div class="tips">
                <p>提款认证必须，请务必记住！</p>
            </div>
            <ul>
                <li>
                    <div class="fl">
                        <i class="ico-i2"></i>
                        <span>确认取款密码：</span>
                    </div>
                    <input v-model="newPasswordc" maxlength="22" type="text" style="-webkit-text-security:disc;" placeholder="确认新提款密码">
                </li>
            </ul>
            <div class="public-btn mt25">
                <button @click="modify" class="button">确认修改</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                oldPassword: '',
                newPassword: '',
                newPasswordc: '',

                UserSession: {},
            }
        },
        created(){
            var user = this.$user()
            this.$store.commit('User/SET_USER', user);

            if(this.env.app){
                var json = '{"title":"取款密码管理","rightTitle":{"name" :"","url":""},"showNai":"y","back":"h5"}';
                this.callHandle(json);
            }

            this.getUserSession()
        },
        methods: {
            getUserSession(){
                const _this = this
                this.$api.user.getUserSession(this.baseUser)
                .then(res=>{
                    this.UserSession = res
                })
            },
            modify(){
                if(this.newPassword != this.newPasswordc){
                    this.$toast('两次密码不一致！')
                    return;
                }
                var oldPassword = this.$md5(this.oldPassword)
                var newPassword = this.$md5(this.newPassword)
                let data = {
                    ...this.baseUser,
                    oldPassword,
                    newPassword,
                }
                this.$api.user.resetDrawPassword(data)
                .then(res=>{
                    console.log(res);
                    if(res.result==1){
                        this.$toast('修改成功');
                        this.oldPassword='';
                        this.newPassword='';
                        this.newPasswordc='';
                        this.UserSession.hasSetDrawPassword = true;
                    }else{
                        this.$toast(res.description)
                    }
                })
            }
        }
    }
</script>

